<template>
  <div class="appSurvey" v-loading="loading">
    <div class="appSurveyContainer">
      <div class="headContainer">
        <div class="pagetarge">
          <!-- <span class="">自定义事件</span> -->
          <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="自定义事件" name="left"></el-tab-pane>
            <el-tab-pane label="用户点击排行" name="right"></el-tab-pane>
          </el-tabs>
          <span class="arget">
            <div class="argetTxt" >
              <h3>指标：</h3>
              <p>浏览量（PV）：当天网站的页面浏览量</p>
              <p>访客数（UV）：当天网站的页面浏览用户数</p>
              <p>IP数（IP）：当天网站的页面浏览ip数</p>
              <p>视频播放量：当天网站的视频总播放量</p>
              <p>人均播放量：当天网站的视频播放量与视频总播放人数比</p>
              <p>人均观看时长：当天网站的视频播放时长与视频总播放人数比</p>
            </div>
          </span>
        </div>
      </div>
      <div class="surveyContainer" v-show="activeName=='left'">
        <div class="videoDataTop">
          <el-row :gutter="20">
            <el-col :span="8" class="box">
              <span>时间：</span>
              <el-date-picker
                v-model="echartDefaultDate"
                value-format="yyyy-MM-dd"
                type="daterange"
                align="right"
                unlink-panels
                range-separator="至"
                start-placeholder="开始"
                end-placeholder="结束"
                :picker-options="pickerOptions"
                class="commonClass"
                >
              </el-date-picker>
            </el-col>
            <el-col :span="8" class="box">
              <span>事件名称：</span>
              <el-select v-model="tagValue" multiple collapse-tags placeholder="请选择" class="commonClass1">
                <el-option
                  v-for="item in tagOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-col>
          </el-row>
          <div class="Allbtn">
            <el-row class="btndata">
              <el-button type="primary" @click="searchBtn()">查询</el-button>
            </el-row>
             <!-- <a class="exportBtn" :href=downloadExcelUrl :disabled=disabled>导出</a> -->
          </div>
        </div>
        <div class="videoDataBottom">
          <el-table
            :data="recentData"
            style="width: 100%"
            :header-cell-style="{background:'#E2E2EB',color:'#333'}">
            <el-table-column
              prop="dateTime"
              label="日期"
              align="center">
            </el-table-column>
            <el-table-column
              prop="world"
              label="事件名称"
              align="center">
            </el-table-column>
            <el-table-column
              prop="pv"
              label="事件数量"
              align="center">
            </el-table-column>
            <el-table-column
              prop="uv"
              label="事件人数"
              align="center">
            </el-table-column>
          </el-table>
        </div>
      </div>
      <div class="surveyContainer" v-show="activeName=='right'">
        <div class="videoDataTop">
          <el-row :gutter="20">
            <el-col :span="8" class="box">
              <span>时间：</span>
              <el-date-picker
                v-model="userDefaultDate"
                value-format="yyyy-MM-dd"
                type="daterange"
                align="right"
                unlink-panels
                range-separator="至"
                start-placeholder="开始"
                end-placeholder="结束"
                :picker-options="pickerOptions"
                class="commonClass"
                @change="changeUserTime"
              >
              </el-date-picker>
            </el-col>
          </el-row>
        </div>
        <div class="videoDataBottom">
          <el-table
            :data="recentData"
            style="width: 100%"
            :header-cell-style="{background:'#E2E2EB',color:'#333'}">
            <el-table-column type="index" label="序号" align="center"></el-table-column>
            <el-table-column prop="nickname" label="用户昵称" align="center"></el-table-column>
            <el-table-column prop="userid" label="用户id" align="center"></el-table-column>
            <el-table-column prop="clickpv" label="点击商品次数" align="center"></el-table-column>
          </el-table>
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage4"
            :page-sizes="[50, 100, 150, 200]"
            :page-size="50"
            layout="total, sizes, prev, pager, next, jumper"
            :total=totalDate>
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import http from '@/libs/http'
import {URL} from '@/libs/url'
export default {
  name: 'videoData',
  data () {
    return {
      title:'用户行为数据',
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '全部',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7499);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      echartDefaultDate: [],//自定义点击
      userDefaultDate:[],//用户点击
      tagValue:[],
      tagOptions:[
        {
          label:'全部',
          value:0,
        },
        {
          label:'上热门按钮点击',
          value:1,
        },
        {
          label:'上热门tab选中',
          value:2,
        },
        {
          label:'上热门支付按钮的点击',
          value:3,
        },
        {
          label:'支付成功订单',
          value:4,
        }
      ],
      recentData:[],//数据查询结果
      currentPage:1,
      pageIndex:1,
      pageIndex1:1,
      loading:false,
      downloadExcelUrl:'',//数据导出
      disabled:true,
      activeName:"left",
      currentPage4:1,
      totalDate:0,//数量
      pageSize:50,//默认展示50条数据
    }
  },
  created(){
    document.title = this.title;
  },
  activated (){
    document.title = this.title;
  },
  mounted() {
    this.echartDefaultDate.push(this.common.formatDate(-3),this.common.formatDate(0))
    this.userDefaultDate.push(this.common.formatDate(-3),this.common.formatDate(0))
    this.searchBtn();
  },
  methods: {
    changeUserTime(){
      this.loading = true;
      var that = this;
      var data = {
        starttime:that.userDefaultDate[0],
        endtime:that.userDefaultDate[1],
        pageIndex: that.pageIndex1,
        pageSize: that.pageSize,
      };
      data = JSON.stringify(data);
      http({  
        //这里是你自己的请求方式、url和data参数
        method: 'POST',
        url:URL.recordUrl.UserClickAction,
        data: data,
        //假设后台需要的是表单数据这里你就可以更改
        headers: {
          "Content-Type":"application/json",
        }
      }).then(function (res) {
        that.loading = false;
        that.recentData = res.data.data;
        that.totalDate = res.data.count;
        // that.downloadExport();
      }).catch(function (err) {
        console.log(err);
      });
      var detailList = [];
      var detailArr = [];
      detailArr[0] =  that.userDefaultDate[0]+'至'+that.userDefaultDate[1];
      detailArr[1] =  that.tagValue;
      detailArr[2] =  "用户点击排行榜";
      detailList.push(detailArr);
      detailList = JSON.stringify(detailList);
      window.LogList.checkLog('二郎神后台管理系统',that.title,'查询',detailList);
    },
    handleClick(tab, event) {
      if(tab.name=="left"){
        this.activeName = "left";
        this.searchBtn();
      }else if(tab.name=="right"){
        this.activeName = "right";
        this.changeUserTime();
      }
    },
    //自定义行为查询数据
    //用户行为数据查询
    searchBtn() {
      if(this.tagValue.length==0){
        this.tagValue = [1,2,3];
      }else if(this.tagValue.includes(0)){
        this.tagValue = [1,2,3];
      }
      this.loading = true;
      var that = this;
      var data = {
        startTime:that.echartDefaultDate[0],
        endTime:that.echartDefaultDate[1],
        ActionType:that.tagValue,
      };
      data = JSON.stringify(data);
      http({  
        //这里是你自己的请求方式、url和data参数
        method: 'POST',
        url:URL.recordUrl.bdgAppUserAction,
        data: data,
        //假设后台需要的是表单数据这里你就可以更改
        headers: {
          "Content-Type":"application/json",
        }
      }).then(function (res) {
        that.loading = false;
        that.recentData = res.data;
        // that.downloadExport();
      }).catch(function (err) {
        console.log(err);
      });
      var detailList = [];
      var detailArr = [];
      detailArr[0] =  that.echartDefaultDate[0]+'至'+that.echartDefaultDate[1];
      detailArr[1] =  that.tagValue;
      detailArr[2] =  "自定义事件";
      detailList.push(detailArr);
      detailList = JSON.stringify(detailList);
      window.LogList.checkLog('二郎神后台管理系统',that.title,'查询',detailList);
    },
    // //数据导出
    // downloadExport() {
    //   this.disabled=false;
    //   this.downloadExcelUrl = URL.recordUrl.getvideoExport+"?MediaName="+this.tagValue+"&startTime="+this.echartDefaultDate[0]+"&endTime="+this.echartDefaultDate[1]+"&ActionType="+this.UserIdValue+"&pageIndex="+this.pageIndex+"&pageSize=30"
    // },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pageSize = val;
      this.changeUserTime()

    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.pageIndex1 = val;
      this.changeUserTime();
    },
    videoTypeOr (row) {
      return row.isplubic==100?'小视频':(row.distributable === 200 ? '横屏视频' : '全部')
    },
    distributableOr (row) {
      return row.distributable==0?'否':(row.distributable === 1 ? '是' : '全部')
    },

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
 @import '../../assets/css/bdgUseraction.less';
 /deep/.el-dropdown-link{
   cursor:pointer;
 }
 /deep/.el-range-editor.el-input__inner{
  width: 25%;
  height: 35px;
  padding: 0px 10px;
}
/deep/.el-tabs__nav-wrap::after{
  background-color:#fff;
}
</style>
